// export default Header;
import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { Sidebar } from "primereact/sidebar";
import { Button } from "primereact/button";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import "./Header.scss";

function Header() {
  const [isSidebarVisible, setIsSidebarVisible] = useState(false);
  const location = useLocation(); //     useLocation 

  return (
    <header className="header">
      <div className="header__logo">
        <img src={require("../assets/logo.png")} alt="" />
      </div>

      {/*     */}
      <nav className="header__nav header__nav--large">
        <Link to="/" className={location.pathname === "/" ? "active" : ""}>
          
        </Link>
        <Link
          to="/search"
          className={location.pathname === "/search" ? "active" : ""}
        >
           
        </Link>
        <Link
          to="/stats"
          className={location.pathname === "/stats" ? "active" : ""}
        >
           
        </Link>
        {/* <Link
          to="/stats2"
          className={location.pathname === "/stats2" ? "active" : ""}
        >
           ()
        </Link> */}
        <Link
          to="/info"
          className={location.pathname === "/info" ? "active" : ""}
        >
           
        </Link>
      </nav>

      {/*       */}
      <Button
        type="button"
        onClick={() => setIsSidebarVisible(true)}
        icon="pi pi-bars"
        rounded
        outlined
        className="header__menu-toggle"
      ></Button>

      {/* Sidebar     */}
      <Sidebar
        visible={isSidebarVisible}
        onHide={() => setIsSidebarVisible(false)}
        position="right"
        className="sidebar"
      >
        <div className="sidebar__header">
          <img
            src={require("../assets/logo.png")}
            alt=""
            className="logo"
          />
          <div className="description">   </div>
        </div>

        <nav className="sidebar__nav">
          <Link
            to="/"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/" ? "active" : ""}
          >
            <span className="icon pi pi-home"></span> 
          </Link>
          <Link
            to="/search"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/search" ? "active" : ""}
          >
            <span className="icon pi pi-search"></span>  
          </Link>
          <Link
            to="/stats"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/stats" ? "active" : ""}
          >
            <span className="icon pi pi-chart-bar"></span>  
          </Link>
          <Link
            to="/info"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/info" ? "active" : ""}
          >
            <span className="icon pi pi-info-circle"></span>  
          </Link>
        </nav>
      </Sidebar>
    </header>
  );
}

export default Header;
